<template>
  <img
    ref="img"
    src=""
    alt=""
  >
</template>
<script>
export default {
  props: {
    url: {
      type: String,
      default: ''
    },
    loading: {
      type: Boolean,
      default: false
    },
    loadError: {
      type: Boolean,
      default: false
    },
    authParams:{
      type:Object,
      default:null
    }
  },
  watch: {
    url(val) {
      this.$emit('update:loading', true)
      this.$refs.img.src = ''
      this.requestImage(val, this.$refs.img)
    }
  },
  mounted() {
    this.$emit('update:loading', true)
    this.requestImage(this.url, this.$refs.img)
  },
  methods: {
    requestImage(url, element) {
      const request = new XMLHttpRequest()
      request.responseType = 'blob'
      request.open('get', url, true)
      if(this.authParams && Object.keys(this.authParams).length > 0){
        Object.keys(this.authParams).forEach(key=>{
          request.setRequestHeader(key, this.authParams[key])
        })
      }
      request.onreadystatechange = e => {
        console.log('onready state change ', e)
        if (request.readyState === XMLHttpRequest.DONE) {
          this.$emit('update:loading', false)
          if (request.status === 200) {
            element.src = URL.createObjectURL(request.response)
            element.onload = () => {
              URL.revokeObjectURL(element.src)
            }
          } else {
            element.alt = '加载失败'
            this.$emit('update:loadError', true)
          }
        } else {
          this.$emit('update:loading', false)
        }
      }
      request.send(null)
    }
  }
}
</script>